<template>
    <div class="singer">

        <div class="singer-hot">
            <van-nav-bar
                left-arrow
                @click-left="tohome"
            />
        </div>
        <div class="singer-list">
              <van-index-bar  >
                <van-index-anchor index="热门" />
                <div class="list-data" v-for="(item,index) in map.hot.items" :key="index" >
                    <div class="list-icon fl" ><img class="img-auto" :src="item.src" alt=""></div>
                    <div class="list-name fl">{{item.name}}</div>
                </div>
              
                 
                
            </van-index-bar>

        </div>
        <!-- 底部 -->
        <div class="singer-player">
                <div class="player clearfix">
                <div class="index-left fl">
                <div class="cleaxfix">
                    <span class="fl"><img src="../indeximg/光碟.png" alt=""></span>
                    <p class="fl">QQ音乐 让生活充满音乐</p>
                </div>
                </div>
                <div class="index-right fr">
                <div class="clearfix">
                    <span class="fl"><img src="../indeximg/播放.png" alt=""></span>
                <span class="fl"><img src="../indeximg/音乐列表-01.png" alt=""></span>
                </div>
                </div> 
            </div>
        </div>
    </div>
</template>
<script>
 import {getSingerList} from '../../api/singers'

const HOT_SINGER_LEN = 10
const HOT_NAME ='热门'
export default {
    name:'singer',
    data(){
        return{
        map :{
          hot: {
            title:HOT_NAME,
            items:[]
          }
        },
      

            
        }
    },
   created() {
     this._getSingerList()
},
//
methods: {
     _getSingerList() {
        getSingerList().then((res) => {
           
            this._normalizeSinger(res.data.list)     
        });      
      },    
         _normalizeSinger(list) {     
        list.forEach((item,index) => {     
            if(index <HOT_SINGER_LEN){
                this.map.hot.items.push({id:item.Fsinger_mid,name:item.Fsinger_name,src:`https://y.gtimg.cn/music/photo_new/T001R300x300M000${item.Fsinger_mid}.jpg?max_age=2592000`})
            }   
          })       
            
      } , 
      tohome(){
        this.$router.push({name:"indexmusic"})
      }  
  }
}
</script>
<style lang="scss" scoped>
.singer-hot{
    width: 100%;
    height: 40px;
    background-color: #fafafa;
    position: fixed;
    top: 0;
    left: 0;
    span{
        margin-left: 15px;
        width: auto;
        height: 40px;
        display: block;
        line-height: 40px;
        text-align: center;
        color: Gray  ;
    }
}
.singer-list{
    margin-top:40px;
    margin-bottom: 50px;
    .list-data{
        width: 100%;
        height: 80px;
       
        .list-icon{
            width: 60px;
            height: 60px;
            margin: 10px;
            .img-auto{
                border-radius: 50%;
            }
        }
        .list-name{
            width: auto;
            height: 30px;
            line-height: 30px;
            margin: 20px 0 10px 0   ;
            font-size: 16px;
        }
    }
}
// 底部
     .player{
            border-top:1px solid #Ccc;
            width: 100%;
            height: 50px;
            background-color: #ffffff;
            position: fixed;
            bottom: 0px;
            z-index: 100;
            .index-left{
              
              div{
              
                span{
                  display:block;
                  height: 35px;
                  width: 35px;
                  padding: 10px;
                  img{
                    width: 100%;
                    height: 100%;

                  }
                }
                p{
                  padding: 8px;
                  color:#8a8a8a;

                }
              }
            }
            .index-right{
              div{
                margin-top: 12px;
                margin-right: 20px;
                    span{
                display:block;
                height: 30px;
                width: 30px;
                margin: 0 8px;

                img{
                  width: 100%;
                  height: 100%;
                }
              }
              }
          
            }
          }
</style>